.sq-payment-form {
  color: #373F4A;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  position: relative;
  width: 380px;
}

.sq-fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

.sq-label {
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0.5;
  text-transform: uppercase;
}

.sq-input {
  box-sizing: border-box;
  border: 1px solid #E0E2E3;
  border-radius: 4px;
  outline-offset: -2px;
  display: inline-block;
  transition: border-color .2s ease-in-out, background .2s ease-in-out;
}

.sq-input--focus {
  border: 1px solid #4A90E2;
  background-color: rgba(74,144,226,0.02);
}
.sq-input--error {
  border: 1px solid #E02F2F;
  background-color: rgba(244,47,47,0.02);
}

.sq-creditcard {
  width: 100%;
  height: 56px;
  margin-top: 10px;
  background: #4A90E2;
  border-radius: 4px;
  cursor: pointer;
  display: block;
  color: #FFFFFF;
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  letter-spacing: 0;
  text-align: center;
  transition: background .2s ease-in-out;
  border: 0;
}

.sq-creditcard:hover {
  background-color: #4281CB;
}

.sq-error {
  color: red;
  text-align: left;
}

.sq-divider {
  height: 24px;
  margin: 24px 0 24px;
  color: #CCC;
  font-size: 14px;
  font-weight: bold;
  line-height: 24px;
  letter-spacing: 0.5;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
}

.sq-divider-label {
  padding: 0 24px;
  background-color: #FFF;
}

.sq-divider-hr {
  margin-top: -12px;
}

.sq-google-pay {
  width: 100%;
  height: 48px;
