.sq-masterpass {
  width: 100%;
  height: 48px;
  padding: 0;
  background-image: url("https://masterpass.com/dyn/img/btn/global/mp_chk_btn_290x048px.svg");
  background-color: black;
  background-size: 100% auto;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  border-radius: 4px;
  cursor: pointer;
  display: none;
  margin-bottom: 10px;
  border: 0;
}

.sq-apple-pay {
  width: 100%;
  height: 48px;
  padding: 0;
  background-image: -webkit-named-image(apple-pay-logo-white);
  background-color: black;
  background-size: 100% 60%;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  border-radius: 4px;
  cursor: pointer;
  display: none;
  margin-bottom: 10px;
  border: 0;
}

.sq-error-message {
  width: 100%;
  margin-top: 16px;
  font-size: 14px;
  color: red;
  font-weight: 500;
  text-align: left;
  opacity: 0.8;
}

.sq-form-third {
  float: left;
  width: calc((100% - 32px) / 3);
  padding: 0;
  margin: 0 16px 16px 0;
}

.sq-form-third:last-of-type {
  margin-right: 0;
}


.sq-wallet-loading {
  background: #E0E2E3;
  border-radius: 6px;
  min-height: 40px;
  -webkit-transform: matrix(-1, 0, 0, 1, 0, 0);
          transform: matrix(-1, 0, 0, 1, 0, 0);
  position: relative;
  margin-bottom: 10px;
}

.sq-wallet-loading::after {
  position: absolute;
  width: 120px;
  height: 6px;
  right: 0;
  left: 0;
  margin: auto;
  bottom: 0;
  top: 0;
  background: #FFFFFF;
  opacity: 0.5;
  border-radius: 50px;
  content: "";
}

.sq-wallet-unavailable {
  background: #FFFFFF;
  border-radius: 6px;
  border: 1px solid #E0E2E3;
  box-sizing: border-box;
  height: 40px;
  width: 100%;
  font-size: 12px;
  padding:10px;
  margin-bottom: 10px;
}

